/* WineDetailEditForm.css */

/* Card for the wine detail page */
.wine-detail-card {
  background-color: #fff;
  max-width: 800px;
  margin: 20px;
  padding: 0px;
  position: relative;
  border: 0px;
  border-radius: 8px; 
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  align-items: center;
}

/* Header styling */
.wine-detail-header {
  text-align: center;
  object-fit: cover; /* Ensure image scales proportionally */
  padding: 0px;
  background-color: #8b3a3a;
  border-top-left-radius: 8px;
  border-top-right-radius: 8px;
}

/* Header text styling */
.wine-detail-header h1 {
  font-size: 1.3rem;
  text-align: center;
  padding: 10px;
  color: white; /* Set header text color to white */
}

/* Image styling */
.wine-detail-image {
  background-color: #fff;
  object-fit: cover; /* Ensure image scales proportionally */
  width: 100%;
  max-width: 1200px; /* Max width for desktop */
  height: auto;
  border-radius: 0px;
  margin: 0px;
  border: 0px;
  box-shadow: 0 0px 0px rgba(0, 0, 0, 0.0);
}

/* Wine details styling */
.wine-detail-info {
  font-size: 1rem;
  line-height: 1.6;
  color: #555;
  padding: 15px;
  text-align: left;
  background-color: #fff;
  flex: 1; /* Make the info section grow to fill available space */
  border-bottom-left-radius: 8px;
  border-bottom-right-radius: 8px;
}

.wine-detail-info p {
  margin: 10px 0;
}

.wine-detail-info strong {
  color: #333;
}

/* Error and loading states */
.wine-detail-error,
.wine-detail-loading {
  text-align: center;
  font-size: 1.2rem;
  color: #d9534f;
  margin-top: 20px;
}

.wine-detail-loading {
  color: #8b3a3a;
}

.back-link {
  padding: 10px;
  text-decoration: underline;
  text-align: left;
}

.age-indicator-container {
  margin-top: 10px;
  text-align: center;
}

.age-progress-bar {
  width: 100%;
  background-color: #e0e0e0; /* Light gray background */
  border-radius: 5px;
  overflow: hidden;
  height: 20px; /* Height of the progress bar */
}

.age-progress {
  height: 100%;
  background-color: #4caf50; /* Green color for the progress */
  transition: width 0.3s ease; /* Smooth transition for the progress */
}

.success-notification {
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #dff0d8;
  color: #3c763d;
  padding: 10px 20px;
  border-radius: 5px;
  border: 1px solid #d6e9c6;
  margin: 10px;
  font-size: 16px;
  font-weight: bold;
  text-align: center;
  transition: opacity 0.3s ease-in-out;
}

.success-icon {
  font-size: 20px;
  margin-right: 10px;
}

.share-button-container {
  width:100%;
  position: relative;
  align-content: right;
}

.share-wine-button {
  background-color: #29ea77;
  border-radius: 8px;
  color: #fff;

}

/* Responsive adjustments */
@media (max-width: 600px) {
  .wine-detail-image {
      max-width: 600px; /* Max width for mobile */
  }
}
